.popup-form {
  .popup__body {
    max-width: 750px;
    border-radius: 5px;
    padding: 59px 108px;
    &_s-padding {
      padding: 63px 59px 80px;
      .popup__close {
        top: 43px;
        right: 59px;
        @media screen and (max-width: 560px) {
          top: 20px;
          right: 20px;
        }
      }
    }
    @media screen and (max-width: 560px) {
      padding: 36px 24px;
    }
  }
  .popup__close {
    width: 15px;
    height: 15px;
    top: 59px;
    right: 108px;
    background: url(../img/popup-form-close.svg) 0 0 no-repeat;

  }
}